@charset "UTF-8";
.pay-success{
  text-align: center;
  border-bottom: 1px solid #ccc;
  padding: 10px 0;
}
.pay-status{
  display: -webkit-box;
  -webkit-box-pack: center;
  -webkit-box-align: center;
  -webkit-box-orient: vertical;
  padding: 10% 4% 4% 4%;
  width: 92%;

.pay-order{
  border: 1px solid;
  text-align: center;
  width: 80%;
  margin-top: 50px;
}
.consult{
  border: 1px solid;
  text-align: center;
  width: 100%;
  margin-top: 50px;
}
}